<template>
    <section class="photo_page">
        <ul class="photo_page_box">
            <li class="photo_list" v-for="img in imgList">
                <h2 class="title">{{img.content}}</h2>
                <div class="img">
                    <img :src="img.url">
                </div>
            </li>
        </ul>
        <div class="change_img" @click="changeImg">换一波</div>
    </section>
</template>

<script>

import api from '@/api/api'
import axios from 'axios'

export default{
    components: {
    },
    data(){
        return{
            imgList: []
        }
    },
    //计算
    computed: {
    },
    //实例已经创建完成之后执行
    created() {
    },
    //完成挂载执行
    mounted() {
        this.getPhoto();
    },
    //事件
    methods: {
        changeImg() {
            this.getPhoto()
        },
        getPhoto() {
            this.$emit('loading',true);
            let data = {
                page: 1,
                catid: 34
            }
            let url="https://bird.ioliu.cn/joke/rand"
            // api.photo(data).then((res) => {
            //     console.log(5,res)
            //     this.$nextTick(() => {
            //         this.$emit('loading',false);
            //     })
            // })
            axios.get(bird+url).then( function(res) {
                console.log(5,res.data.data)
                this.imgList = res.data.data
                this.$nextTick(() => {
                    this.$emit('loading',false);
                })
            }.bind(this)).catch(function (error) {
                    console.log("网络有问题")
                    console.log(error)
                })
        }
    }
}

</script>

<style lang="scss">

@import '../style/mixin.scss';

.photo_page{
    .photo_page_box{
        padding: 10px/$ppr;
        .photo_list{
            margin-bottom: 10px/$ppr;
            .title{
                line-height: 30px/$ppr;
                word-break: break-all;
                word-wrap: break-word;
            }
            .img{
                font-size: 14px/$ppr;
                & img{
                    width: 100%;
                }
            }
        }
    }
    .change_img{
        position: fixed;
        right: -20px/$ppr;
        bottom: 20%;
        width: 100px/$ppr;
        height: 40px/$ppr;
        line-height: 40px/$ppr;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.80);
        border-radius: 20rem;
        box-shadow: 0px 0px 10px rgb(255, 255, 255);
        color: white;
    }
}

    
</style>


